import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss']
})
export class NavbarComponent implements OnInit {

title = 'app';

home = [
  {
    name: 'Home',
    route: 'home'
  }
];

portfolio = [
  {
    name: 'Contact',
    route: 'contact'
  }
];

resume = [
  {
    name: 'Resume',
    route: 'resume'
  }
];

tabs = [
  this.home,
  this.resume,
  this.portfolio
]

  constructor() { }

  ngOnInit() {
  }

}



